<template>
  <div id="app">
    <h1>Flex</h1>

      <p> flex . </p>
      <p>Flex Flexible Box</p>

      <p>
        <small>
          <i> Flex floatclearvertical-align</i>
        </small>
      </p>
      <pre>
  .box{
    display: flex; /* Flex */
    display: inline-flex; /* Flex */
    display: -webkit-flex; /* Safari -Webkit -webkit*/
    display: -webkit-inline-flex; /* Safari -Webkit -webkit*/
  }
      </pre>

      <h3>( row wrap maxHeight:500px minHeight:150px; width:auto )</h3>
      <Container :flexObj='derectionObj'></Container>
      <Container :flexObj='wrapObj'></Container>
      <Container :flexObj='flowObj'></Container>
      <Container :flexObj='justifyContentObj'></Container>
      <Container :flexObj='alignItemsObj'></Container>
      <Container :flexObj='alignContentObj'></Container>

      <Items></Items>
  </div>
</template>

<script>
  import Container from './container.vue'
  import Items from './items.vue'


  export default {
    name: 'app',
    components: {
      Container,
      Items
    },
    data() {
      return {

        derectionObj: {
          title: 'flex-direction',
          info: '',
          currentValue: 'row',
          value: [{
              'name': 'row',
              'info': ')'
            },
            {
              'name': 'row-reverse',
              'info': ''
            },
            {
              'name': 'column',
              'info': ''
            },
            {
              'name': 'column-reverse',
              'info': ''
            }
          ],
        },

        wrapObj: {
          title: 'flex-wrap',
          info: '',
          currentValue: 'nowrap',
          value: [{
              'name': 'nowrap',
              'info': ''
            },
            {
              'name': 'wrap',
              'info': ''
            },
            {
              'name': 'wrap-reverse',
              'info': ''
            }
          ],
        },

        flowObj: {
          title: 'flex-flow',
          info: 'flex-directionflex-wrap',
          currentValue: 'row nowrap',
          value: [{
            'name': 'row nowrap',
            'info': ''
          }, {
            'name': 'row wrap',
            'info': ''
          }, {
            'name': 'row wrap-reverse',
            'info': ''
          }, {
            'name': 'row-reverse nowrap',
            'info': ''
          }, {
            'name': 'row-reverse wrap',
            'info': ''
          }, {
            'name': 'row-reverse wrap-reverse',
            'info': ''
          }, {
            'name': 'column nowrap',
            'info': ''
          }, {
            'name': 'column wrap',
            'info': ''
          }, {
            'name': 'column wrap-reverse',
            'info': ''
          }, {
            'name': 'column-reverse nowrap',
            'info': ''
          }, {
            'name': 'column-reverse wrap',
            'info': ''
          }, {
            'name': 'column-reverse wrap-reverse',
            'info': ''
          }],
        },

        justifyContentObj: {
          title: 'justify-content',
          info: '',
          currentValue: 'flex-start',
          value: [{
            'name': 'flex-start',
            'info': ''
          }, {
            'name': 'flex-end',
            'info': ''
          }, {
            'name': 'center',
            'info': ''
          }, {
            'name': 'space-between',
            'info': ''
          }, {
            'name': 'space-around',
            'info': ''
          }, ],
        },

        alignItemsObj: {
          title: 'align-items',
          info: '',
          currentValue: 'stretch',
          value: [{
            'name': 'stretch',
            'info': ',auto'
          }, {
            'name': 'flex-start',
            'info': ''
          }, {
            'name': 'flex-end',
            'info': ''
          }, {
            'name': 'center',
            'info': ''
          }, {
            'name': 'baseline',
            'info': ''
          }, ],
        },

        alignContentObj: {
          title: 'align-content',
          info: '()',
          currentValue: 'stretch',
          value: [{
            'name': 'stretch',
            'info': ','
          }, {
            'name': 'flex-start',
            'info': ''
          }, {
            'name': 'flex-end',
            'info': ''
          }, {
            'name': 'center',
            'info': ''
          }, {
            'name': 'space-between',
            'info': ''
          }, {
            'name': 'space-around',
            'info': ''
          }, ],
        },

      }
    },
    methods: {}
  }

</script>

<style lang="scss">
  $pre: rgb(57, 114, 199);
  $font: gray;

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; // text-align: center;
    max-width: 1200px;
    margin: 0 auto;
    margin-top: 60px;
  }

  small {
    color: #666;
  }

  h1,
  h2 {
    color: $font;
    font-weight: normal;
    border-bottom: 2px solid gray;
  }

  pre {
    font-size: 15px;
    background-color: $font;
    /*border-radius: 7px;*/
    padding: 10px;
    color: #fff;
    overflow-x: auto;
  }

  p {
    background-color: #f5f5f5;
    padding: 10px;
    a {
      cursor: pointer;
      padding: 2px;
      /*border-radius: 5px;*/
      color: gray;
      &:hover {
        background-color: $font;
        color: white;
      }
    }
  }

</style>



